import PropTypes from 'prop-types';
import { useRef } from 'react';
// @mui
import { useTheme, styled } from '@mui/material/styles';
import { Box, Stack, Paper, Grid, Button, Avatar, Container, Typography } from '@mui/material';
// _mock
import _mock from '../../_mock';
// components
import Image from '../../components/Image';
import TextMaxLine from '../../components/TextMaxLine';
import { MotionInView, varFade } from '../../components/animate';
import Iconify from '../../components/Iconify';
// utils
import { fDateTime } from '../../utils/formatTime';
import cssStyles from '../../utils/cssStyles';
// hooks
// import useRouter from '../../hooks/useRouter';
// ----------------------------------------------------------------------

const allStream = [...Array(15)].map((_, index) => ({
  id: _mock.id(index),
  cover: _mock.image.cover(index),
  title: _mock.text.title(index),
  name: _mock.name.fullName(index),
  avatar: _mock.image.avatar(index),
  date: _mock.time(index),
  active: false,
}));

const lastStream = [...Array(5)].map((_, index) => ({
  id: _mock.id(index),
  cover: _mock.image.cover(index),
  title: _mock.text.title(index),
  name: _mock.name.fullName(index),
  avatar: _mock.image.avatar(index),
  date: _mock.time(index),
  active: _mock.boolean(index),
}));

const upComingStream = [...Array(3)].map((_, index) => ({
  id: _mock.id(index),
  cover: _mock.image.cover(index),
  title: _mock.text.title(index),
  name: _mock.name.fullName(index),
  avatar: _mock.image.avatar(index),
  date: _mock.time(index),
  active: false
}));

// ----------------------
const BottomOverWidget = styled('div')(({ theme }) => ({
  ...cssStyles(theme).bgBlur({ blur: 4}),
  position: 'absolute', bottom: 0, left: 0, right: 0, zIndex: 99,
  display: 'flex', alignItems: 'center',
  padding: theme.spacing(1, 2),
  backgroundColor: 'rgba(0,0,0,0.3)',
  
}));

// -------------
export default function StreamMore() {
  const theme = useTheme();

  return (
    <>
      <Paper sx={{ p: 1 }}>
        <MotionInView variants={varFade().inUp}>
          <Typography variant="h4" sx={{ mb: 1 }}>
            Up Coming
          </Typography>
        </MotionInView>
        <Grid container spacing={3} columns={{ lg: 5, md: 3, sm: 2, xs: 1 }}>
          {upComingStream.map((item, index) => (
            <Grid item key={index} xs={1}>
              <ShowCard item={item} />
            </Grid>
          ))}
        </Grid>
      </Paper>
      <Box sx={{ p: 1 }}>
        <MotionInView variants={varFade().inUp}>
          <Typography variant="h4" sx={{ mb: 1 }}>
            More Stream
          </Typography>
        </MotionInView>
        <Grid container spacing={2} columns={{ lg: 5, md: 3, sm: 2, xs: 1 }}>
         
          {allStream.map((item, index) => (
            <Grid item key={index} xs={1}>
              <ShowCard item={item} />
            </Grid>
          ))}
        </Grid>
      </Box>


    </>

  );
}

// ----------------------------------------------------------------------

ShowCard.propTypes = {
  item: PropTypes.object.isRequired,

};

function ShowCard({ item }) {
  const theme = useTheme();
  const { avatar, name, title, cover, date, active } = item;
  return (
    <Paper sx={{
      my: 2, borderRadius: 1, bgcolor: 'background.neutral', position: 'relative', overflow: 'hidden',
      border: '1px solid lightgrey',
      '&:hover': { boxShadow: `0px 4px 10px 3px ${theme.palette.grey[500_80]}` }
    }}>
      <Image src={cover} ratio="5/4" sx={{ borderRadius: 1 }} />
      <Box
        sx={{
          left: 10, zIndex: 9, top: 10, position: 'absolute',
          bgcolor: active ? 'primary.main' : 'grey.100080',
          color: 'white', borderRadius: 1, px: 1, fontSize: 12
        }} >
        {active ? "Live" : fDateTime(date)}
      </Box>
      <Box
        sx={{
          left: 10, zIndex: 9, top: 10, position: 'absolute',
          bgcolor: active ? 'primary.main' : 'grey.100080',
          color: 'white', borderRadius: 1, px: 1, fontSize: 12
        }} >
        {active ? "Live" : fDateTime(date)}
      </Box>

      <BottomOverWidget >
        <Stack direction="row" alignItems="center" spacing={1}>
          <Avatar alt={name} src={avatar} sx={{ border: '2px solid white' }} />
          <div>
            <TextMaxLine sx={{ color: '#fff !important', lineHeight: 1 }} variant={"body2"} line={2}>{title}</TextMaxLine>
            <Typography sx={{ color: '#fff', display: 'block', px: 1 }} variant={"caption"}>
              {name}
            </Typography>
          </div>
        </Stack>
      </BottomOverWidget>
    </Paper>
  );
}


