import { useState, useEffect } from 'react';
// @mui
import { useTheme, styled } from '@mui/material/styles';
import { Box, Drawer, Button, Chip, Tab, IconButton, Stack, Dialog, DialogActions, DialogContent, DialogTitle, Tooltip } from '@mui/material';
import { TabContext, TabList, TabPanel } from '@mui/lab';
// hooks

import useResponsive from '../../hooks/useResponsive';
// components
import Iconify from '../../components/Iconify';
import Scrollbar from '../../components/Scrollbar';

import SocialsButton from '../../components/SocialsButton';
//

import VideoTabChat from './VideoTabChat';
import VideoTabDetails from './VideoTabDetails';
import VideoTabImages from './VideoTabImages';
import VideoTabBuy from './VideoTabBuy';
// ----------------------------------------------------------------------

const SIDEBAR_WIDTH = 320;

export default function VideoSideBarRight() {

  const theme = useTheme();
  const [openSidebar, setOpenSidebar] = useState(true);
  const isDesktop = useResponsive('up', 'lg');
  const [tabValue, setTabValue] = useState('chatstab');
  const [openDialog, setOpenDialog] = useState(false);

  useEffect(() => {
    if (!isDesktop) {
      return handleCloseSidebar();
    }
    return handleOpenSidebar();
  }, [isDesktop]);


  // -------speed actionControl------
  const handleAction = (action) => {
    if (action === 'share') {
      setOpenDialog(true);
    }
    else {
      setTabValue((`${action}tab`));
      setOpenSidebar(true);
    }
  }
  // -------sidebarControl--------

  const handleOpenSidebar = () => {
    setOpenSidebar(true);
  };
  const handleCloseSidebar = () => {
    setOpenSidebar(false);
  };
  const handleToggleSidebar = (e) => {
    setOpenSidebar(!openSidebar);
  }

  // --------sharedialogcontrol----------
  const handleCloseDialog = () => {
    setOpenDialog(false);
  }

  // ---------tabcontrol----------
  const handleChange = (event, newValue) => {
    setTabValue(newValue);
  };

  const VIDEO_TAB = [
    {
      value: 'chatstab',
      label: 'Chat',
      disabled: false,
    },
    {
      value: 'detailstab',
      label: 'Details',
      disabled: false,
    },
    {
      value: 'imagestab',
      label: 'Images',
      disabled: false,
    },
    {
      value: 'buytab',
      label: 'Buy',
      disabled: false,
    },
  ];

  const TOGGLEICONS = [
    {
      name: 'chats',
      icon: <Iconify icon="majesticons:chats-2" width={24} height={24} />,
      color: "primary.light"
    },
    {
      name: 'details',
      icon: <Iconify icon="majesticons:checkbox-list-detail" width={24} height={24} />,
      color: "primary.light"
    },
    {
      name: 'images',
      icon: <Iconify icon="majesticons:image" width={24} height={24} />,
      color: "primary.light"
    },
    {
      name: 'buy',
      icon: <Iconify icon="majesticons:shopping-cart" width={24} height={24} />,
      color: "primary.light"
    },
    {
      name: 'share',
      icon: <Iconify icon="ci:share" width={24} height={24} />,
      color: "primary.light"
    }
  ];

  const renderContent = (

    <TabContext value={tabValue} >
      <TabList onChange={handleChange}
        sx={{

          borderRadius: 1,
          mb: 1,
        }}
      >
        {VIDEO_TAB.map((tab, index) => (
          <Tab
            key={index}
            label={tab.label}
            value={tab.value}
            disabled={tab.disabled}
            sx={{ mx: "0 !important", flexGrow: 1 }} />
        ))}
      </TabList>

      <TabPanel value="chatstab">
        <VideoTabChat />
      </TabPanel>
      <Scrollbar>
        <TabPanel value="detailstab">
          <VideoTabDetails />
        </TabPanel>
        <TabPanel value="imagestab">
          <VideoTabImages />
        </TabPanel>
        <TabPanel value="buytab">
          <VideoTabBuy />
        </TabPanel>
      </Scrollbar>

    </TabContext>

  );

  return (
    <Box sx={{ position: 'relative' }}>
      <Box sx={{
        position: "absolute", transition: "all 0.3s",
        top: (!isDesktop || (openSidebar && isDesktop)) ? "45%" : "20%",
        right: (openSidebar ? (SIDEBAR_WIDTH + 15) : 15)
      }} >
        {((isDesktop && openSidebar) || (!openSidebar && !isDesktop)) &&
          <IconButton
            onClick={handleToggleSidebar}
            sx={{ mb: 2, border: '2.5px solid', zIndex: 10 }}
          >
            <Iconify icon={openSidebar ? 'ep:arrow-right-bold' : 'ep:arrow-left-bold'} />
          </IconButton>
        }

        <Stack spacing={2} sx={{ display: ((!openSidebar && isDesktop) ? 'block' : 'none') }}>
          {TOGGLEICONS.map((action, index) => (
            <Tooltip key={index} placement="left" title={action.name} arrow>
              <IconButton
                key={action.name}
                onClick={() => handleAction(action.name)}
                sx={{
                  border: '2px solid',
                  borderColor: action.color,
                  color: action.color,

                }}
              >
                {action.icon}
              </IconButton>
            </Tooltip>
          ))}
        </Stack>


      </Box>


      {isDesktop ? (
        <Drawer
          open={openSidebar}
          anchor="right"
          variant="persistent"
          onClose={handleCloseSidebar}
          sx={{
            height: 1,
            width: SIDEBAR_WIDTH,
            transition: theme.transitions.create('width'),
            ...(!openSidebar && { width: '0px' }),
            '& .MuiDrawer-paper': {
              position: 'static',
              width: SIDEBAR_WIDTH,
            },
          }}
        >
          {renderContent}
        </Drawer>
      ) : (
        <Drawer
          anchor="right"
          ModalProps={{ keepMounted: true }}
          open={openSidebar}
          onClose={handleCloseSidebar}
          sx={{
            '& .MuiDrawer-paper': {
              width: SIDEBAR_WIDTH,
            },
          }}
        >
          {renderContent}
        </Drawer>
      )}
      <Dialog
        open={openDialog}
        onClose={handleCloseDialog}
        aria-describedby="alert-dialog-slide-description"
      >
        <DialogTitle>{"share this show"}</DialogTitle>
        <DialogContent>
          <SocialsButton initialColor sx={{ width: 40, height: 40 }} />
        </DialogContent>
      </Dialog>
    </Box>
  );
}





