import { useState, useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
// @mui
import { useTheme, styled } from '@mui/material/styles';
import { Box, Stack, Drawer, IconButton, Typography, Divider } from '@mui/material';
// hooks
import useResponsive from '../../hooks/useResponsive';
// utils
import axios from '../../utils/axios';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// components
import Iconify from '../../components/Iconify';
import Scrollbar from '../../components/Scrollbar';
//
import CreaterSearchResult from './CreaterSearchResult';
import CreaterContactSearch from './CreaterContactSearch';
//
import _mock from '../../_mock';

// ----------------------------------------------------------------------

const _creaters = [...Array(14)].map((_, index) => ({
  id: _mock.id(index),
  name: _mock.name.fullName(index),
  avatar: _mock.image.avatar(index),
}));



// ----------------------------------------------------------------------

const SIDEBAR_WIDTH = 240;
const SIDEBAR_COLLAPSE_WIDTH = 90;

export default function VideoSideBarLeft() {
  const theme = useTheme();

  const navigate = useNavigate();

  const { pathname } = useLocation();

  const [openSidebar, setOpenSidebar] = useState(true);

  const [searchQuery, setSearchQuery] = useState('');

  const [searchResults, setSearchResults] = useState([]);

  const [isSearchFocused, setSearchFocused] = useState(false);

  const isDesktop = useResponsive('up', 'md');

  const isCollapse = isDesktop && !openSidebar;

  useEffect(() => {
    if (!isDesktop) {
      return handleCloseSidebar();
    }
    return handleOpenSidebar();
  }, [isDesktop, pathname]);

  // eslint-disable-next-line consistent-return
  useEffect(() => {
    if (!openSidebar) {
      return setSearchFocused(false);
    }
  }, [openSidebar]);

  const handleOpenSidebar = () => {
    setOpenSidebar(true);
  };

  const handleCloseSidebar = () => {
    setOpenSidebar(false);
  };

  const handleToggleSidebar = () => {
    setOpenSidebar((prev) => !prev);
  };

  const handleClickAwaySearch = () => {
    setSearchFocused(false);
    setSearchQuery('');
  };

  const handleChangeSearch = async (event) => {
    try {
      const { value } = event.target;
      setSearchQuery(value);
      if (value) {
        const response = await axios.get('/api/chat/search', {
          params: { query: value },
        });
        setSearchResults(response.data.results);
      } else {

      }
    } catch (error) {
      console.error(error);
    }
  };

  const handleSearchFocus = () => {
    setSearchFocused(true);
  };

  const handleSearchSelect = (username) => {
    setSearchFocused(false);
    setSearchQuery('');
    navigate(`${PATH_DASHBOARD.chat.root}/${username}`);
  };

  const handleSelectContact = (result) => {
    if (handleSearchSelect) {
      handleSearchSelect(result.username);
    }
  };

  const renderContent = (
    <>
      {!isCollapse && (
        <Box sx={{ px: 1 }}>
          <CreaterContactSearch
            query={searchQuery}
            onFocus={handleSearchFocus}
            onChange={handleChangeSearch}
            onClickAway={handleClickAwaySearch}
          />
          <Typography paragraph variant="subtitle1" sx={{ p: 1, mb: 0, color: 'text.secondary' }}>
            Featured Creators
          </Typography>
        </Box>
      )}
      <Divider />
      < Scrollbar >
        <CreaterSearchResult query={searchQuery} results={_creaters} onSelectContact={handleSelectContact} />
      </Scrollbar>
      <Divider />
    </>
  );

  return (

    <>
      {(isDesktop || (!isDesktop && !openSidebar)) &&
        <Box sx={{
          position: "absolute", transition: "all 0.3s", zIndex: 10, top: "45%",
          left: openSidebar
            ? (SIDEBAR_WIDTH + 15)
            : (isDesktop? SIDEBAR_COLLAPSE_WIDTH + 15 :15)
        }}>

          <IconButton
            onClick={handleToggleSidebar}
            sx={{ mb: 2, border: '2.5px solid', zIndex: 10 }}        >
            <Iconify icon={openSidebar ? 'ep:arrow-left-bold' : 'ep:arrow-right-bold'}
            />
          </IconButton>
        </Box>
      }
      {isDesktop ? (
        <Drawer
          open={openSidebar}
          variant="persistent"
          sx={{
            width: SIDEBAR_WIDTH,
            transition: theme.transitions.create('width'),
            '& .MuiDrawer-paper': {
              position: 'static',
              width: SIDEBAR_WIDTH,
            },
            ...(isCollapse && {
              width: SIDEBAR_COLLAPSE_WIDTH,
              '& .MuiDrawer-paper': {
                width: SIDEBAR_COLLAPSE_WIDTH,
                position: 'static',
                transform: 'none !important',
                visibility: 'visible !important',
              },
            }),
          }}
        >
          {renderContent}
        </Drawer>
      ) : (
        <Drawer
          ModalProps={{ keepMounted: true }}
          open={openSidebar}
          onClose={handleCloseSidebar}
          sx={{
            '& .MuiDrawer-paper': { width: SIDEBAR_WIDTH },
          }}
        >
          {renderContent}
        </Drawer>
      )}
    </>
  );
}
