import PropTypes from 'prop-types';
// @mui
import { List, Avatar, Box, ListItemText, ListItemAvatar, ListItemButton } from '@mui/material';
//
import SearchNotFound from '../../components/SearchNotFound';

// ----------------------------------------------------------------------

CreaterSearchResult.propTypes = {
  query: PropTypes.string,
  results: PropTypes.array,
  onSelectContact: PropTypes.func,
};

export default function CreaterSearchResult({ query, results, onSelectContact }) {
  const isFound = results.length > 0;

  return (
    <Box sx={{my:1}}>
      <List disablePadding>
        {results.map((result) => (
          <ListItemButton
            key={result.id}
            onClick={() => onSelectContact(result)}
          >
            <ListItemAvatar>
              <Avatar alt={result.name} src={result.avatar} sx={{width:55, height:55,border:'2px solid white'}}/>
            </ListItemAvatar>
            <ListItemText
              primary={result.name}
              primaryTypographyProps={{
                noWrap: true,
                variant: 'subtitle2',
              }}
            />
          </ListItemButton>
        ))}
      </List>
      {!isFound && (
        <SearchNotFound
          searchQuery={query}
          sx={{
            p: 2,
            mx: 'auto',
            width: `calc(100% - 48px)`,
            bgcolor: 'background.neutral',
          }}
        />
      )}
    </Box>
  );
}
